#whole-app {
  height: 100vh;
  width: 100vw;
  background-color: black;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

#Header {
  height: 10%;
  width: 90%;
  display: flex;
  align-items: top;
  justify-content: center;
  font-size: 60%;
  padding-bottom: 10%;
}

#Main {
  display: flex;
  justify-content: space-evenly;
  align-items: top;
  text-align: center;
  min-height: 50vmin;
  min-width: 90%;
  padding-left: 5%;
  padding-bottom: 10%;
}

h1,
h3,
#break-length,
#session-length {
  color: seashell;
}

.incDec {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: yellow;
  font-size: 100%;
  font-weight: bold;
  min-width: 10vmin;
  min-height: 10vmin;
  border-radius: 100%;
}

.flex-third {
  min-height: 70vmin;
  min-width: 30vmin;
  border-radius: 5px;
  background-color: gray;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

#timer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

#timer-label,
#break-label,
#session-label {
  height: 5vh;
  font-size: 100%;
}

#time-left {
  text-align: center;
  background-color: darkslateblue;
  color: limegreen;
  border-radius: 100%;
  font-size: 200%;
  line-height: 20vmin;
  min-width: 20vmin;
  min-height: 20vmin;
}

.timer-buttons {
  display: flex;
  justify-content: space-between;
  align-items: top;
  color: whitesmoke;
  min-width: 30vmin;
  min-height: 10vmin;
  font-size: 100%;
}

#start_stop {
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(
    to right,
    rgb(43, 197, 43),
    rgb(196, 196, 34)
  );
  min-height: 10vmin;
  min-width: 40%;
  border-radius: 10%;
  padding: 1vmin;
  font-size: 100%;
}
#reset {
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(
    to right,
    rgb(196, 196, 34),
    rgb(240, 4, 51)
  );
  border-radius: 5px;
  min-height: 10vmin;
  min-width: 40%;
  padding: 1vmin;
  font-size: 100%;
}
.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 10vh;
  width: 5vw;
  pointer-events: none;
}

@keyframes App-logo-spin {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(360deg);
  }
  100% {
    transform: rotate(0deg);
  }
}
